import { BrowserRouter, Route, Switch } from 'react-router-dom'
import Home from './pages/home'
import Counter from './pages/counter'

function App() {
  return (
    <BrowserRouter>
      <div />
      <Switch>
        <Route path='/counter'>
          <Counter />
        </Route>
        <Route path='/' exact>
          <Home />
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default App;
